<script>
    import screenfull from "screenfull";

    export default {
        metaInfo() {
            return {
                title: this.$i18n.t('title.luckyDraw'),
                meta: []
            }
        },
        data() {
            return {
                style: {
                    fontSize: 15,
                    title: "尾牙抽獎",
                },
                drawData: {
                    showIndex: -1,
                    drawViewSourceList: [],
                    drawViewList: [],
                    personCurrentList: [],
                    itemCurrentList: [],
                    currentDrawTarget: null,
                    status: "init"
                },
                drawType: 'drawPersonForItem',
                personInputType: 'text',
                personText: "",
                personOriginList: [
                    {name: "Boo"},
                    {name: "Foo"},
                    {name: "Puck"},
                    {name: "Yuuna"},
                    {name: "Apple"},
                    {name: "Benana"},
                    {name: "Cat"},
                    {name: "Dog"},
                    {name: "Egg"},
                    {name: "Qoo"},
                    {name: "Yahoo"},
                    {name: "Google"},
                    {name: "Pig"},
                ],
                itemInputType: 'text',
                itemText: "",
                itemInputNumberType: "number",
                itemOriginList: [
                    {name: '一獎', number: 1},
                    {name: '二獎', number: 2},
                ],
                log: []
            }
        },
        methods: {
            parsePersonText() {
                let persons = []
                this.personText.split("\n").forEach((value) => {
                    persons.push({
                        name: value
                    });
                });
                this.personOriginList = persons;
            },
            parseitemText() {
                let items = [];
                this.itemText.split("\n").forEach((value) => {
                    let item = value.split(",");
                    items.push({
                        name: item[0],
                        number: item[1] * 1
                    })
                });
                this.itemOriginList = items;
            },
            fullscreen() {
                screenfull.request($("#luckyDrawMain")[0]);
            },
            initDrawData() {
                this.drawData.itemCurrentList = []
                this.itemOriginList.forEach(value => {
                    this.drawData.itemCurrentList.push(value);
                });
                this.drawData.personCurrentList = []
                this.personOriginList.forEach(value => {
                    this.drawData.personCurrentList.push(value);
                });
                this.drawData.status = 'ready';

                if (this.drawType === 'drawPersonForItem' || this.drawType === 'drawPerson') {
                    this.drawData.drawViewSourceList = [];
                    this.personOriginList.forEach(value => {
                        this.drawData.drawViewSourceList.push(value);
                    });

                } else if (this.drawType === 'drawItemForPerson' || this.drawType === 'drawItem') {
                    this.drawData.drawViewSourceList = [];
                    this.itemOriginList.forEach(value => {
                        this.drawData.drawViewSourceList.push(value);
                    });
                }
                this.settingNextDrawData();
            },
            settingNextDrawData() {
                if (this.drawData.personCurrentList.length === 0 || this.drawData.itemCurrentList.length === 0) {
                    this.drawData.status = 'end';
                } else {
                    if (this.drawType === 'drawPersonForItem') {
                        this.drawData.currentDrawTarget = this.drawData.itemCurrentList[this.drawData.itemCurrentList.length-1];
                    } else if (this.drawType === 'drawItemForPerson') {
                        this.drawData.currentDrawTarget = this.drawData.personCurrentList[this.drawData.personCurrentList.length-1];
                    }
                    this.drawData.status = 'ready';
                }
            },
            settingDrawView() {
                this.drawData.drawViewList = [];
                for (let i = 0; i < 20; i++) {
                    this.drawData.drawViewList.push(this.drawData.drawViewSourceList[this.getRandomNumber(this.drawData.drawViewSourceList.length)]);
                }
                this.drawData.status = 'drawing';
            },
            drawing() {
                let winIndex, item, person;

                switch (this.drawType) {
                    case 'drawPersonForItem':
                        item = this.drawData.itemCurrentList.pop();
                        item.number--;
                        if (item.number > 0) {
                            this.drawData.itemCurrentList.push(item);
                        }
                    case 'drawPerson':
                        winIndex = this.getRandomNumber(this.drawData.personCurrentList.length);
                        person = this.drawData.personCurrentList[winIndex];
                        this.drawData.personCurrentList.splice(winIndex, 1);
                        this.drawData.drawViewList.push(person);
                        break;
                    case 'drawItemForPerson':
                        person = this.drawData.personCurrentList.pop();
                    case 'drawItem':
                        winIndex = this.getRandomNumber(this.drawData.itemCurrentList.length);
                        item = this.drawData.itemCurrentList[winIndex];
                        item.number--;
                        if (item.number === 0) {
                            this.drawData.itemCurrentList.splice(winIndex, 1);
                        }
                        this.drawData.drawViewList.push(item);
                        break;
                }

                this.writeLog(item, person);
            },
            draw() {
                this.settingDrawView();
                this.drawing();
                this.showDrawView();
                console.log(this.data);
            },
            writeLog(item, person) {
                this.log.push({
                    item: item,
                    person: person
                });
            },
            showDrawView() {
                let self = this;
                let interval;
                let length = self.drawData.drawViewList.length;
                let winIndex = length - 1;
                interval = setInterval(function () {
                    self.drawData.showIndex = self.getRandomNumber(length);
                }, 100);
                setTimeout(function () {
                    clearInterval(interval);
                    self.drawData.showIndex = winIndex;
                    self.drawData.status = 'confirm';
                }, 3000);
            },
            getRandomNumber(max) {
                return Math.floor(Math.random() * max);
            }
        },
        computed: {},
        watch: {},
        components: {},
        mounted() {

        }
    }
</script>

